body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  position: relative;
  min-height: 100vh;
}

.content{
  padding-bottom: 5rem;
  margin: 0 auto;
}

a {
  -webkit-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out;
  }
  a:hover {
  color:red;
}
.HeaderDiv{
    background-color: rgb(36, 39, 49);
    padding-top: 2%;
    height: 15vh;
    width: 100%;
}

.HeaderInnerDiv{
    margin: 0 auto;
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
}

.InfoNav, .ProjectsNav, .HomeNav{
    padding: 2%;
    font-size: 150%;
    color:white;
}

.FooterDiv{
    background-color:  rgb(36, 39, 49);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    bottom: 0;
    height: 15vh;
    position: absolute;
    width: 100%;
}

.FooterInnerDiv{
    margin: 0 auto;
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
}

